import { ComponentProps } from "react"
import { Box, styled } from "@rp/react/web/mui/core"

type HubTableRowWrapperProps = ComponentProps<typeof Box> & {
  isMobile: boolean
}

const HubTableRowWrapper = styled(Box, {
  name: "HubTableRowWrapper",
  shouldForwardProp: propName => propName !== "isMobile",
})<HubTableRowWrapperProps>(({ theme, isMobile }) => {
  return {
    alignItems: "center",
    borderBottom: isMobile ? undefined : `1px solid #515151`,
    borderRadius: isMobile ? "8px" : undefined,
    display: "flex",
    height: isMobile ? 56 : 48,
    justifyContent: "space-between",
    marginBottom: isMobile ? theme.spacing(1) : undefined,
    marginTop: isMobile ? theme.spacing(1) : undefined,
    paddingLeft: isMobile ? theme.spacing(1) : theme.spacing(2),
    paddingRight: isMobile ? theme.spacing(1) : theme.spacing(2),
    width: "100%",
  }
})

export { HubTableRowWrapper }
